.card{
  border-radius: 10px;
  height: 340px;
  width: 340px;
  background: rgba(255, 255, 255, 0);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-blend-mode: darken;
  display: flex;
  align-items: flex-end;
  margin: 2%;
  cursor: pointer;
  text-decoration: none;
}

.thumbnail-section {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    background-color: #F6F6F6;
    border-radius: 25px;
    margin-top: 30px;
    padding: 30px;
    max-width: 80%;
}

.parent{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title{
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 142.6%;
    padding: 20px;
    color: white;
}



@media screen and (max-width: 768px) {
    .card {
        margin: 20px 0 0 0;
        height: 255px;
        width: 100%;
      }
  
}